.navbar{display:flex;padding:0.5rem;justify-content:flex-end}.navbar_options{display:flex;align-items:center;justify-content:center}.navbar_option{padding:0.5rem}.navbar_option:hover{cursor:pointer}.header_subtitle h3{font-size:1rem;font-weight:normal;color:#868e96}

.card-container{padding:0.5rem;display:flex}.card{width:100%;max-width:300px;margin:0.5rem 0;display:flex;align-items:center;justify-content:space-between;padding:1rem;width:100%;background-color:#8bbe8a;border-radius:1rem;max-height:90px;transition:all 0.2s ease}.card:hover{cursor:pointer;box-shadow:0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06)}.card-img{position:relative;top:-10px}.card-img img{width:100%;height:auto;max-width:160px;min-width:160px;z-index:0}.card-data-id span{font-size:0.9rem;font-weight:600;color:#212529}.card-data-name span{font-weight:600;font-size:1.6rem;color:#fff}.card-data-types{align-items:center;justify-content:center;margin:0.5rem 0;display:flex}.card-data-types .fas{margin-right:0.2rem}.card-data-typeA{display:flex;align-items:center;justify-content:center}.card-data-typeA span{display:flex;font-size:0.8rem;padding:0.3rem;margin:0 0.2rem 0 0;background-color:#62b958;color:#fff;border-radius:0.4rem}.card-data-typeB{display:flex;align-items:center;justify-content:center}.card-data-typeB span{display:flex;font-size:0.8rem;padding:0.3rem;margin:0 0 0 0.2rem;background-color:#a552cc;color:#fff;border-radius:0.4rem}.card-img-circle{display:flex;justify-content:center;align-items:center;max-height:130px;max-width:130px;background-color:rgba(255,255,255,0.6);border-radius:50%;text-align:center}.card-data-kanji span{color:#ffffffb6;font-size:1rem;width:200px}

.header{margin:0 auto;display:flex;padding:0.5rem;justify-content:center;flex-direction:column;margin-bottom:1rem}.header_title h1{font-weight:900;margin:0}.header_subtitle h3{font-weight:400;margin:0.5rem 0}.header_search{margin-top:1rem;display:flex;justify-content:center;align-items:center}.header_search-bar{max-width:300px;display:flex;background-color:#f2f4f4;border-radius:1rem;padding:1rem;width:100%}.header_search-input{display:flex;width:100%}.header_search-input input{background-color:transparent;border:none;width:100%}.fa-search{padding-right:0.5rem}.fa-search:hover{cursor:pointer}

